import genPassword from './mod-generator';

const passwordInput = document.querySelector('#password-input');
const charAmount = document.querySelector('#character-amount');
const checkLowercase = document.querySelector('#check-lowercase');
const checkUppercase = document.querySelector('#check-uppercase');
const checkNumber = document.querySelector('#check-number');
const checkSymbol = document.querySelector('#check-symbol');
let copyIcon = document.querySelector('#copy-icon');
let checkIcon = document.querySelector('#check-icon');

export default () => {
    document.querySelector('#generate-btn').addEventListener('click', () => {
        passwordInput.value = genPassword(charAmount.value, checkLowercase.checked, checkUppercase.checked, checkNumber.checked, checkSymbol.checked);
        if (checkIcon.style.display === 'block') {
            checkIcon.style.display = 'none';
            copyIcon.style.display = 'block';
        }
    });
};

