let copyIcon = document.querySelector('#copy-icon');
let checkIcon = document.querySelector('#check-icon');
export default () => {
    document.querySelector('.copy-icon-box').addEventListener('click', () => {
        let inputValue = document.querySelector('#password-input').value;
        if (inputValue.length > 0) {
            navigator.clipboard.writeText(inputValue);
            copyIcon.style.display = 'none';
            checkIcon.style.display = 'block';
        }
    })
}