const rand = (min, max) => Math.floor(Math.random() * (max - min) + min);

const genLowercase = () => String.fromCharCode(rand(97, 123));
const genUppercase = () => String.fromCharCode(rand(65, 91));
const genNumber = () => String.fromCharCode(rand(48, 58));

const symbols = '!@#$%&*-+';
const genSymbol = () => symbols[rand(0, symbols.length)];

export default function genPassword(amount, lowercase, uppercase, number, symbol) {
    const password = [];
    amount = Number(amount);
    for (let i = 0; i < amount; i++) {
        lowercase && password.push(genLowercase());
        uppercase && password.push(genUppercase());
        number && password.push(genNumber());
        symbol && password.push(genSymbol());
    }

    return password.join('').slice(0, amount) || ':(';
};